import React, { useEffect, useState, useRef } from 'react';
import { useSelector } from 'react-redux';
import axios from 'axios';
import * as d3 from 'd3';

const monthNames = {
  1: 'January',
  2: 'February',
  3: 'March',
  4: 'April',
  5: 'May',
  6: 'June',
  7: 'July',
  8: 'August',
  9: 'September',
  10: 'October',
  11: 'November',
  12: 'December'
};

const Pie = () => {
  const selectedMonth = useSelector((state) => state.month); // Get the current month from the Redux store
  const [data, setData] = useState([]);
  const svgRef = useRef();

  useEffect(() => {
    const fetchData = async () => {
      try {
        const response = await axios.get(`/api/category/${selectedMonth}`);
        setData(Object.entries(response.data).map(([category, count]) => ({ category, count })));
      } catch (error) {
        console.error('Error fetching data:', error);
      }
    };

    fetchData();
  }, [selectedMonth]);

  useEffect(() => {
    if (data.length > 0) {
      drawChart();
    }
  }, [data]);

  const drawChart = () => {
    const svg = d3.select(svgRef.current);
    const width = +svg.attr('width');
    const height = +svg.attr('height');
    const radius = Math.min(width, height) / 2;
    const color = d3.scaleOrdinal(d3.schemeCategory10);

    svg.selectAll('*').remove();

    const g = svg.append('g')
      .attr('transform', `translate(${width / 2}, ${height / 2})`);

    const pie = d3.pie().value(d => d.count);
    const path = d3.arc().outerRadius(radius - 10).innerRadius(0);
    const label = d3.arc().outerRadius(radius - 40).innerRadius(radius - 40);

    const arc = g.selectAll('.arc')
      .data(pie(data))
      .enter().append('g')
      .attr('class', 'arc');

    arc.append('path')
      .attr('d', path)
      .attr('fill', d => color(d.data.category));

    arc.append('text')
      .attr('transform', d => `translate(${label.centroid(d)})`)
      .attr('dy', '0.35em')
      .text(d => `${d.data.category}: ${d.data.count}`);
  };

  return (
    <div className="w-full max-w-md mx-auto p-4">
    <h1 className="text-xl font-bold mb-4 bg-gray-300 text-black rounded-md">

      
      Category Pie Chart of {monthNames[selectedMonth]}</h1>
      <svg ref={svgRef} width="500" height="400"></svg>
    </div>
  );
};

export default Pie;
