import React, { useState, useEffect } from 'react';
import axios from 'axios';
import { useDispatch, useSelector } from 'react-redux';
import { setMonth } from '../store/monthSlice';

const TotalSale = () => {
    const month = useSelector((state) => state.month); // Get the current month from the Redux store
    const dispatch = useDispatch(); // Get the dispatch function from Redux
    const [sales, setSales] = useState(null);
    const [soldItems, setSoldItems] = useState(null);
    const [unsoldItems, setUnsoldItems] = useState(null);
  
  useEffect(() => {
    const fetchSales = async () => {
      try {
        const response = await axios.get(`/api/sales/${month}`);
        setSales(response.data.totalSaleAmount);
      } catch (error) {
        console.error('Error fetching sales:', error);
      }
    };

    const fetchSoldItems = async () => {
      try {
        const response = await axios.get(`/api/sold/${month}`);
        setSoldItems(response.data.totalSoldItems);
      } catch (error) {
        console.error('Error fetching sold items:', error);
      }
    };

    const fetchUnsoldItems = async () => {
      try {
        const response = await axios.get(`/api/unsold/${month}`);
        setUnsoldItems(response.data.totalUnsoldItems);
      } catch (error) {
        console.error('Error fetching unsold items:', error);
      }
    };

    fetchSales();
    fetchSoldItems();
    fetchUnsoldItems();
  }, [month]);

  return (
    <div className="w-1/1 min-w-min bg-gray-100 p-4 rounded-md">
      <h1 className="text-xl font-bold mb-4 rounded-md">Total Sales</h1>
      <select
        className="form-select block w-full mt-1 mb-4 rounded-md"
        value={month}
        onChange={(e) => dispatch(setMonth(e.target.value))} // Dispatch the setMonth action when the selected month changes
      >
        <option value="1">January</option>
        <option value="2">February</option>
        <option value="3">March</option>
        <option value="4">April</option>
        <option value="5">May</option>
        <option value="6">June</option>
        <option value="7">July</option>
        <option value="8">August</option>
        <option value="9">September</option>
        <option value="10">October</option>
        <option value="11">November</option>
        <option value="12">December</option>
      </select>
      <div className="flex justify-between">
        <h2 className="text-2xl font-semibold">{sales !== null ? `Total Sales:$  ${sales.toFixed(2)}` : 'Loading...'}</h2>
        <h2 className="text-2xl font-semibold">{soldItems !== null ? `Total Sold Items: ${soldItems}` : 'Loading...'}</h2>
        <h2 className="text-2xl font-semibold">{unsoldItems !== null ? `Total Unsold Items: ${unsoldItems}` : 'Loading...'}</h2>
      </div>
    </div>
  );
};

export default TotalSale;
