import React, { useEffect, useState } from "react";

export const Tabledata = () => {
  const [data, setData] = useState([]);
  const [currentPage, setCurrentPage] = useState(1);
  const [itemsPerPage] = useState(10);

  useEffect(() => {
    fetch("/api/data")
      .then((response) => response.json())
      .then((data) => setData(data))
      .catch((error) => console.error(error));
  }, []);

  // Get current items
  const indexOfLastItem = currentPage * itemsPerPage;
  const indexOfFirstItem = indexOfLastItem - itemsPerPage;
  const currentItems = data.slice(indexOfFirstItem, indexOfLastItem);

  return (
    <div className="overflow-x-auto rounded-md" >
      <table className="table-auto w-full rounded-md">
        <thead>
          <tr className="bg-yellow-200">
            <th className="px-4 py-2">ID</th>
            <th className="px-4 py-2">Title</th>
            <th className="px-4 py-2">Description</th>
            <th className="px-4 py-2">Price</th>
            <th className="px-4 py-2">Category</th>
            <th className="px-4 py-2">Sold</th>
            <th className="px-4 py-2">Image</th>
          </tr>
        </thead>
        <tbody>
          {currentItems.map((item) => (
            <tr key={item.id} className="border-b border-blue-300 hover:bg-green-100">
              <td className="px-4 py-2">{item.id}</td>
              <td className="px-4 py-2">{item.title}</td>
              <td className="px-4 py-2">{item.description}</td>
              <td className="px-4 py-2">{item.price}</td>
              <td className="px-4 py-2">{item.category}</td>
              <td className="px-4 py-2">{item.sold ? "Yes" : "No"}</td>
              <td className="px-4 py-2">
                <img src={item.image} alt="Product" className="h-10 w-10" />
              </td>
            </tr>
          ))}
        </tbody>
      </table>
      <div className="pagination flex items-center justify-center mt-4">
        <button 
          onClick={() => setCurrentPage(currentPage > 1 ? currentPage - 1 : currentPage)}
          className="mr-5 bg-blue-500 hover:bg-blue-700 text-white font-bold py-2 px-4 rounded-l focus:outline-none"
        >
          Previous
        </button>
        <span className="px-4">Page {currentPage}</span>
        <button 
          onClick={() => setCurrentPage(currentPage < Math.ceil(data.length / itemsPerPage) ? currentPage + 1 : currentPage)}
          className="ml-5 bg-blue-500 hover:bg-blue-700 text-white font-bold py-2 px-4 rounded-r focus:outline-none"
        >
          Next
        </button>
      </div>
    </div>
  );
};
